.opportunities-container {
  padding: 5vw 0;
  .opportunities-title-container {
    .main-title {
      margin-left: 14vw;
      margin-bottom: 1vw;
      letter-spacing: 4px;
    }
    .subtitle {
      width: 13vw;
      margin: 0;
    }
  }
  .opportunities-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .opportunities-image-block-container {
    display: flex;
    justify-content: flex-end;
    padding: 5vw 0;
    .opportunities-image-block {
      width: 75vw;
      height: 25vw;
      background-color: black;
    }
  }
  .opportunities-form-container {
    display: flex;
    justify-content: space-between;
    padding: 14vw;
    .opportunities-form-left-container, .opportunities-form-right-container {
      width: 30vw;
    }
    .opportunities-form-left-container {
      .subtitle {
        margin-top: 0;
        padding-left: 0;
      }
    }
    .opportunities-form-right-container {

    }
  }

}
